import React, { useState } from 'react';
import { useAuth0 } from "@auth0/auth0-react";
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

export const SeekerForm = () => {
    const { getAccessTokenSilently } = useAuth0();
    const [formData, setFormData] = useState({
        name: '',
        email: '',
        phoneNumber: '',
        address: '',
    });

    const [errors, setErrors] = useState({});
    const apiUrl = process.env.REACT_APP_API_SERVER_URL + '/v1/abhyaasi';

    const validateForm = () => {
        let isValid = true;
        const newErrors = {};

        // Validate username
        if (!formData.name.trim()) {
            isValid = false;
            newErrors.name = 'Username is required';
        }

        // Validate email
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        if (formData.email.trim().length > 0 && !emailRegex.test(formData.email.trim())) {
            isValid = false;
            newErrors.email = 'Enter a valid email address';
        }

        // Validate phone number
        const phoneRegex = /^\d{10}$/;
        if (formData.phoneNumber.trim().length > 0 && !phoneRegex.test(formData.phoneNumber.trim())) {
            isValid = false;
            newErrors.phoneNumber = 'Enter a valid 10-digit phone number';
        }

        // Validate address
        // if (!formData.address.trim()) {
        //     isValid = false;
        //     newErrors.address = 'Address is required';
        // }

        setErrors(newErrors);
        return isValid;
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        const accessToken = await getAccessTokenSilently();
        if (validateForm()) {
            try {
                const response = await fetch(apiUrl, {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                        Authorization: `Bearer ${accessToken}`,
                    },
                    body: JSON.stringify(formData)
                });

                if (response.ok) {
                    // Handle successful submission
                    toast.success('Seeker added successfully', {
                        position: 'top-right',
                        autoClose: 3000,
                        hideProgressBar: true,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                    });
                    setFormData({
                        name: '',
                        email: '',
                        phoneNumber: '',
                        address: '',
                    });
                } else {
                    // Handle error response
                    toast.error('Failed to add seeker.', {
                        position: 'top-right',
                        autoClose: 3000,
                        hideProgressBar: true,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                    });
                }
            } catch (error) {
                console.error('Error:', error);
                toast.error('Failed to add seeker.', {
                    position: 'top-right',
                    autoClose: 3000,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                });
            }
        }
    };

    const handleChange = (e) => {
        setFormData({
            ...formData,
            [e.target.name]: e.target.value
        });

        // Clear validation error when user starts typing
        setErrors({
            ...errors,
            [e.target.name]: ''
        });
    };

    return (
        <div className="container mt-5">
            <form onSubmit={handleSubmit}>
                <div className="mb-3">
                    <label htmlFor="name" className="form-label">Username:</label>
                    <input
                        type="text"
                        className={`form-control ${errors.name ? 'is-invalid' : ''}`}
                        id="name"
                        name="name"
                        value={formData.name}
                        onChange={handleChange}
                    />
                    <div className="invalid-feedback">{errors.name}</div>
                </div>

                <div className="mb-3">
                    <label htmlFor="email" className="form-label">Email:</label>
                    <input
                        type="text"
                        className={`form-control ${errors.email ? 'is-invalid' : ''}`}
                        id="email"
                        name="email"
                        value={formData.email}
                        onChange={handleChange}
                    />
                    <div className="invalid-feedback">{errors.email}</div>
                </div>

                <div className="mb-3">
                    <label htmlFor="phoneNumber" className="form-label">Phone Number:</label>
                    <input
                        type="text"
                        className={`form-control ${errors.phoneNumber ? 'is-invalid' : ''}`}
                        id="phoneNumber"
                        name="phoneNumber"
                        value={formData.phoneNumber}
                        onChange={handleChange}
                    />
                    <div className="invalid-feedback">{errors.phoneNumber}</div>
                </div>

                <div className="mb-3">
                    <label htmlFor="address" className="form-label">Address:</label>
                    <input
                        type="text"
                        className={`form-control ${errors.address ? 'is-invalid' : ''}`}
                        id="address"
                        name="address"
                        value={formData.address}
                        onChange={handleChange}
                    />
                    <div className="invalid-feedback">{errors.address}</div>
                </div>

                <button type="submit" className="btn btn-primary">Submit</button>
            </form>
            <ToastContainer />
        </div>
    );
};
