import React, { useState, useEffect } from 'react';
import { useTable, useSortBy, useFilters } from 'react-table';
import { Table, Pagination, Form, Modal, Button } from 'react-bootstrap';
import { PageLayout } from "../components/page-layout";
import {useAuth0} from "@auth0/auth0-react";
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

export const SeekerList = () => {
    const { getAccessTokenSilently } = useAuth0();
    const [data, setData] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(50);
    const [selectedSeeker, setSelectedSeeker] = useState(null);
    const [showModal, setShowModal] = useState(false);

    const abhyaasiUrl = '/v1/abhyaasi';
    const apiUrl = process.env.REACT_APP_API_SERVER_URL + abhyaasiUrl;


    const fetchData = async () => {
        try {
            const accessToken = await getAccessTokenSilently();
            const response = await fetch(apiUrl, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${accessToken}`,
                },
            });

            if (response.ok) {
                const result = await response.json();
                setData(result);
            } else {
                console.error('Failed to get seekerlist');
            }
        } catch (error) {
            toast.error('Failed to get seekers.', {
                position: 'top-right',
                autoClose: 3000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
            });
            console.error('Error:', error);
        }

        handleCloseModal();
    };

    useEffect(() => {
        fetchData();
    }, []);

    const TextFilter = ({ column }) => {
        const { filterValue, setFilter } = column;

        return (
            <Form.Control
                value={filterValue || ''}
                onChange={(e) => setFilter(e.target.value)}
                placeholder={`Filter ${column.Header.toLowerCase()}`}
            />
        );
    };

    const columns = React.useMemo(
        () => [
            // { Header: 'Id', accessor: 'id', Filter: TextFilter },
            { Header: 'Username', accessor: 'name', Filter: TextFilter },
            { Header: 'Email', accessor: 'email', Filter: TextFilter },
            { Header: 'Phone Number', accessor: 'phoneNumber', Filter: TextFilter },
            { Header: 'Address', accessor: 'address', Filter: TextFilter },
            { Header: 'Last Sitting Date', accessor: 'lastSittingDate'},
            { Header: 'Status', accessor: 'status', Filter: TextFilter},
        ],
        []
    );

    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,
        rows,
        prepareRow,
        state: { filters }
    } = useTable({ columns, data }, useFilters, useSortBy);

    const totalPages = Math.ceil(rows.length / itemsPerPage);
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = rows.slice(indexOfFirstItem, indexOfLastItem);

    const paginate = (pageNumber) => setCurrentPage(pageNumber);

    const handleEditSeeker = (seeker) => {
        setSelectedSeeker(seeker);
        setShowModal(true);
    };

    const handleCloseModal = () => {
        setSelectedSeeker(null);
        setShowModal(false);
    };

    const handleSaveChanges = async () => {
        try {
            const accessToken = await getAccessTokenSilently();
            const response = await fetch(apiUrl, {
                method: 'PATCH',
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${accessToken}`,
                },
                body: JSON.stringify(selectedSeeker),
            });

            if (response.ok) {
                console.log('Seeker updated successfully!');
                toast.success('seeker updated.', {
                    position: 'top-right',
                    autoClose: 3000,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                });
                fetchData();
            } else {
                throw ('failed to update seeker');
            }
        } catch (error) {
            console.error('Error:', error);
            toast.error('Failed to update seeker.', {
                position: 'top-right',
                autoClose: 3000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
            });
        }

        handleCloseModal();
    };

    return (
        <PageLayout>
            <div className="content-layout">
                <h1 id="page-title" className="content__title">
                    Seekers List
                </h1>
                <div className="content__body">
                    <h1>Seekers List</h1>
                    <div className="container mt-5">
                        <Table {...getTableProps()} striped bordered hover>
                            <thead>
                            {headerGroups.map(headerGroup => (
                                <tr {...headerGroup.getHeaderGroupProps()}>
                                    {headerGroup.headers.map(column => (
                                        <th {...column.getHeaderProps(column.getSortByToggleProps())}>
                                            {column.render('Header')}
                                            <span>
                                                    {column.isSorted
                                                        ? column.isSortedDesc
                                                            ? ' '
                                                            : ' '
                                                        : ''}
                                                </span>
                                            {column.canFilter && column.id !== 'lastSittingDate' && <div>{column.render('Filter')}</div>}
                                        </th>
                                    ))}
                                </tr>
                            ))}
                            </thead>
                            <tbody {...getTableBodyProps()}>
                            {currentItems.map(row => {
                                prepareRow(row);
                                return (
                                    <tr {...row.getRowProps()} onClick={() => handleEditSeeker(row.original)} style={{ cursor: 'pointer' }}>
                                        {row.cells.map(cell => (
                                            <td {...cell.getCellProps()}>{cell.render('Cell')}</td>
                                        ))}
                                    </tr>
                                );
                            })}
                            </tbody>
                        </Table>

                        <Pagination>
                            <Pagination.Prev onClick={() => paginate(currentPage - 1)} disabled={currentPage === 1} />
                            {Array.from({ length: totalPages }, (_, i) => (
                                <Pagination.Item key={i + 1} active={i + 1 === currentPage} onClick={() => paginate(i + 1)}>
                                    {i + 1}
                                </Pagination.Item>
                            ))}
                            <Pagination.Next onClick={() => paginate(currentPage + 1)} disabled={currentPage === totalPages} />
                        </Pagination>

                        <Modal show={showModal} onHide={handleCloseModal}>
                            <Modal.Header closeButton>
                                <Modal.Title>Edit Seeker</Modal.Title>
                            </Modal.Header>
                            <Modal.Body>
                                <Form>
                                    <Form.Group controlId="formId">
                                        {/*<Form.Label>Id</Form.Label>*/}
                                        <Form.Control
                                            type="hidden"
                                            value={selectedSeeker ? selectedSeeker.id : ''}
                                            onChange={(e) => setSelectedSeeker({ ...selectedSeeker, id: e.target.value })}
                                            // readOnly // Add this readOnly attribute
                                            // style={{ backgroundColor: '#f0f0f0' }} // Add this style to grey it out
                                        />
                                    </Form.Group>

                                    <Form.Group controlId="formName">
                                        <Form.Label>Name</Form.Label>
                                        <Form.Control
                                            type="text"
                                            placeholder="Enter name"
                                            value={selectedSeeker ? selectedSeeker.name : ''}
                                            onChange={(e) => setSelectedSeeker({ ...selectedSeeker, name: e.target.value })}
                                        />
                                    </Form.Group>

                                    <Form.Group controlId="formPhoneNumber">
                                        <Form.Label>Phone Number</Form.Label>
                                        <Form.Control
                                            type="text"
                                            placeholder="Enter phone number"
                                            value={selectedSeeker ? selectedSeeker.phoneNumber : ''}
                                            onChange={(e) => setSelectedSeeker({ ...selectedSeeker, phoneNumber: e.target.value })}
                                        />
                                    </Form.Group>

                                    <Form.Group controlId="formEmail">
                                        <Form.Label>Email</Form.Label>
                                        <Form.Control
                                            type="email"
                                            placeholder="Enter email"
                                            value={selectedSeeker ? selectedSeeker.email : ''}
                                            onChange={(e) => setSelectedSeeker({ ...selectedSeeker, email: e.target.value })}
                                        />
                                    </Form.Group>

                                    <Form.Group controlId="formCity">
                                        <Form.Label>City</Form.Label>
                                        <Form.Control
                                            type="text"
                                            placeholder="Enter Address"
                                            value={selectedSeeker ? selectedSeeker.address : ''}
                                            onChange={(e) => setSelectedSeeker({ ...selectedSeeker, address: e.target.value })}
                                        />
                                    </Form.Group>
                                </Form>
                            </Modal.Body>
                            <Modal.Footer>
                                <Button variant="secondary" onClick={handleCloseModal}>
                                    Close
                                </Button>
                                <Button variant="primary" onClick={handleSaveChanges}>
                                    Save Changes
                                </Button>
                            </Modal.Footer>
                        </Modal>
                    </div>
                </div>
            </div>
            <ToastContainer />
        </PageLayout>
    );
};
