import React, { useState, useEffect } from 'react';
import { Form, Button, Container, Row, Col } from 'react-bootstrap';
import { Typeahead } from 'react-bootstrap-typeahead';
import { PageLayout } from "../components/page-layout";
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useAuth0 } from "@auth0/auth0-react";
import { useNavigate } from 'react-router-dom';

export const SittingForm = () => {

    const navigate = useNavigate();
    const { getAccessTokenSilently } = useAuth0();
    const [sittingList, setSittingList] = useState([]);
    const [newSitting, setNewSitting] = useState('');
    const [newDate, setNewDate] = useState('');
    const [nameSuggestions, setNameSuggestions] = useState([]);
    const [editIndex, setEditIndex] = useState(null);
    const [isSubmitting, setIsSubmitting] = useState(false);

    const sittingsUrl = process.env.REACT_APP_API_SERVER_URL + '/v1/sittings';
    const sittingsSelectionUrl = sittingsUrl + "/selections";

    useEffect(() => {
        // Set the default date to the current date
        const currentDate = new Date().toISOString().split('T')[0];
        setNewDate(currentDate);

        // Fetch names from your API endpoint for autocomplete suggestions
        const fetchNames = async () => {
            try {
                const accessToken = await getAccessTokenSilently();

                const response = await fetch(sittingsSelectionUrl, {
                    method: 'GET',
                    headers: {
                        'Content-Type': 'application/json',
                        Authorization: `Bearer ${accessToken}`,
                    }
                });
                if (response.ok) {
                    const names = await response.json();
                    setNameSuggestions(names);
                } else {
                    console.error('Failed to fetch names for autocomplete');
                }
            } catch (error) {
                console.error('Error:', error);
            }
        };

        fetchNames();
    }, []);

    const handleAddSitting = () => {
        if (newSitting.name && newDate) {
            if (editIndex !== null) {
                // If editing, update the existing sitting with the original date
                const updatedSittingList = [...sittingList];
                updatedSittingList[editIndex] = { ...newSitting, date: newDate };
                setSittingList(updatedSittingList);
                setEditIndex(null);
            } else {
                // If not editing, add a new sitting with the current date
                setSittingList([...sittingList, { ...newSitting, date: newDate }]);
            }
            setNewSitting('');
            setNewDate('');
        }
    };

    const handleEditSitting = (index) => {
        const sittingToEdit = sittingList[index];
        setNewSitting(sittingToEdit);
        setNewDate(sittingToEdit.date); // Set the date for editing
        setEditIndex(index);
    };

    const handleDeleteSitting = (index) => {
        const updatedSittingList = [...sittingList];
        updatedSittingList.splice(index, 1);
        setSittingList(updatedSittingList);
        setEditIndex(null);
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        if (isSubmitting || sittingList.length === 0) {
            // Prevent duplicate submits or submitting an empty list
            return;
        }

        setIsSubmitting(true);

        try {

            const accessToken = await getAccessTokenSilently();
            const response = await fetch(sittingsUrl, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${accessToken}`,
                },
                body: JSON.stringify({ sittingList }),
            });

            if (response.ok) {
                console.log('Sitting list submitted successfully!');
                toast.success('Sitting list submitted successfully!', {
                    position: 'top-right',
                    autoClose: 3000,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    onClose: () => {
                        // Redirect to /seeker-list after the toast message is closed
                        navigate('/list-seekers');
                    },
                });
                // Clear the sitting list after successful submission
                setSittingList([]);
            } else {
                console.error('Failed to submit sitting list');
                toast.error('Failed to submit sitting list', {
                    position: 'top-right',
                    autoClose: 3000,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                });
            }
        } catch (error) {
            console.error('Error:', error);
            toast.error('Error submitting sitting list', {
                position: 'top-right',
                autoClose: 3000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
            });
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <PageLayout>
            <div className="content-layout">
                <h1 id="page-title" className="content__title">
                    Add Sittings
                </h1>
                <div className="content__body">
                    <h1>Use this page to add sittings</h1>
                    <Container className="mt-5">
                        <Button variant="success" type="submit" onClick={handleSubmit} disabled={isSubmitting || sittingList.length === 0}>
                            Submit Sitting List
                        </Button>
                        <Row>
                            <Col md={{ span: 6, offset: 3 }}>
                                <Form>
                                    <Form.Group controlId="sitting">
                                        <Form.Label>Sitting:</Form.Label>
                                        <Typeahead
                                            id="sitting-typeahead"
                                            labelKey={(option) => `${option.name} (${option.email} ${option.phoneNumber})`}
                                            options={nameSuggestions}
                                            placeholder="Select a seeker"
                                            selected={newSitting ? [newSitting] : []}
                                            onChange={(selected) => setNewSitting(selected[0] || '')}
                                            required
                                        />
                                    </Form.Group>

                                    <Form.Group controlId="date">
                                        <Form.Label>Date:</Form.Label>
                                        <Form.Control
                                            type="date"
                                            value={newDate}
                                            onChange={(e) => setNewDate(e.target.value)}
                                            max={new Date().toISOString().split('T')[0]} // Set min to today
                                        />
                                    </Form.Group>

                                    <Button
                                        variant="primary"
                                        type="button"
                                        onClick={handleAddSitting}
                                        disabled={!newSitting || !newDate}
                                    >
                                        {editIndex !== null ? 'Edit Sitting' : 'Add Sitting'}
                                    </Button>

                                </Form>
                            </Col>
                        </Row>
                        <ul className="mt-3">
                            {sittingList.map((sitting, index) => (
                                <li key={index}>
                                    {sitting.name} - {sitting.date}
                                    <Button
                                        variant="link"
                                        size="sm"
                                        className="mx-2"
                                        onClick={() => handleEditSitting(index)}
                                    >
                                        Edit
                                    </Button>
                                    <Button
                                        variant="link"
                                        size="sm"
                                        onClick={() => handleDeleteSitting(index)}
                                    >
                                        Delete
                                    </Button>
                                </li>
                            ))}
                        </ul>
                    </Container>
                </div>
            </div>
            <ToastContainer />
        </PageLayout>
    );
};
