import {useAuth0} from "@auth0/auth0-react";
import React from "react";
import {Route, Routes} from "react-router-dom";
import {PageLoader} from "./components/page-loader";
import {AuthenticationGuard} from "./components/authentication-guard";
import {AdminPage} from "./pages/admin-page";
import {CallbackPage} from "./pages/callback-page";
import {HomePage} from "./pages/home-page";
import {NotFoundPage} from "./pages/not-found-page";
import {ProfilePage} from "./pages/profile-page";
import {AddSeeker} from "./pages/add-seeker";
import {PublicPage} from "./pages/public-page";
import {PrefectsList} from "./pages/trainers-list";
import {SittingForm} from "./pages/SittingForm";
import {SeekerList} from "./pages/seeker-list";
import {SittingsList} from "./pages/sittings-list";

export const App = () => {
    const {isLoading} = useAuth0();

    if (isLoading) {
        return (
            <div className="page-layout">
                <PageLoader/>
            </div>
        );
    }

    return (
        <Routes>
            <Route path="/" element={<HomePage/>}/>
            <Route
                path="/profile"
                element={<AuthenticationGuard component={ProfilePage}/>}
            />
            <Route path="/public" element={<PublicPage/>}/>
            <Route
                path="/my-sittings"
                element={<AuthenticationGuard component={SittingsList}/>}
            />
            <Route
                path="/add-seeker"
                element={<AuthenticationGuard component={AddSeeker}/>}
            />
            <Route
                path="/list-seekers"
                element={<AuthenticationGuard component={SeekerList}/>}
            />
            <Route
                path="/list-trainers"
                element={<AuthenticationGuard component={PrefectsList}/>}
            />
            <Route
                path="/add-sittings"
                element={<AuthenticationGuard component={SittingForm}/>}
            />
            <Route
                path="/admin"
                element={<AuthenticationGuard component={AdminPage}/>}
            />
            <Route path="/callback" element={<CallbackPage/>}/>
            <Route path="*" element={<NotFoundPage/>}/>
        </Routes>
    );
};
