import React, { useState, useEffect } from 'react';
import { useTable, useSortBy, useFilters } from 'react-table';
import { Table, Pagination, Form } from 'react-bootstrap';
import {PageLayout} from "../components/page-layout";
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import {useAuth0} from "@auth0/auth0-react";

export const PrefectsList = () => {
    const { getAccessTokenSilently } = useAuth0();
    const [data, setData] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(25);

    const trainersUrl = '/v1/trainers';
    const apiUrl = process.env.REACT_APP_API_SERVER_URL + trainersUrl;

    useEffect(() => {
        const fetchData = async () => {
            const accessToken = await getAccessTokenSilently();
            try {
                const response = await fetch(apiUrl, {
                    method: 'GET',
                    headers: {
                        'Content-Type': 'application/json',
                        Authorization: `Bearer ${accessToken}`,
                    },
                });
                if (response.ok) {
                    const result = await response.json();
                    setData(result);
                } else {
                    console.error('Failed to fetch data');
                }
            } catch (error) {
                console.error('Error:', error);
                toast.error('Failed to fetch trainers.', {
                    position: 'top-right',
                    autoClose: 3000,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                });
            }
        };

        fetchData();
    }, []);

    const TextFilter = ({ column }) => {
        const { filterValue, setFilter } = column;

        return (
            <Form.Control
                value={filterValue || ''}
                onChange={(e) => setFilter(e.target.value)}
                placeholder={`Filter ${column.Header.toLowerCase()}`}
            />
        );
    };

    const AddressCell = ({ value }) => (
        <a href={`https://maps.google.com/?q=${encodeURIComponent(value)}`} target="_blank" rel="noopener noreferrer">
            {value}
        </a>
    );

    const PhotoCell = ({ value }) => (
        <a href={value} target="_blank" rel="noopener noreferrer">
        <img src={value} width="64" height="64"/>
        </a>
    );

    const columns = React.useMemo(
        () => [
            { Header: 'Username', accessor: 'name', Filter: TextFilter },
            { Header: 'Email', accessor: 'email', Filter: TextFilter },
            { Header: 'Phone Number', accessor: 'phoneNumber', Filter: TextFilter },
            { Header: 'Address', accessor: 'address', Filter: TextFilter, Cell: AddressCell },
            { Header: 'Photo', accessor: 'photoUrl', Cell: PhotoCell }
        ],
        []
    );

    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,
        rows,
        prepareRow,
        state: { filters }
    } = useTable({ columns, data }, useFilters, useSortBy);

    const totalPages = Math.ceil(rows.length / itemsPerPage);
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = rows.slice(indexOfFirstItem, indexOfLastItem);

    const paginate = (pageNumber) => setCurrentPage(pageNumber);

    return (
        <PageLayout>
            <div className="content-layout">
                <h1 id="page-title" className="content__title">
                    Trainers List
                </h1>
                <div className="content__body">
                    <h1>Trainers List</h1>
                    <div className="container mt-5">
                        <Table {...getTableProps()} striped bordered hover>
                            <thead>
                            {headerGroups.map(headerGroup => (
                                <tr {...headerGroup.getHeaderGroupProps()}>
                                    {headerGroup.headers.map(column => (
                                        <th {...column.getHeaderProps(column.getSortByToggleProps())}>
                                            {column.render('Header')}
                                            <span>
                    {column.isSorted
                        ? column.isSortedDesc
                            ? ' '
                            : ' '
                        : ''}
                  </span>
                                            {column.canFilter && column.id !== 'photoUrl' &&  <div>{column.render('Filter')}</div>}
                                        </th>
                                    ))}
                                </tr>
                            ))}
                            </thead>
                            <tbody {...getTableBodyProps()}>
                            {currentItems.map(row => {
                                prepareRow(row);
                                return (
                                    <tr {...row.getRowProps()}>
                                        {row.cells.map(cell => (
                                            <td {...cell.getCellProps()}>{cell.render('Cell')}</td>
                                        ))}
                                    </tr>
                                );
                            })}
                            </tbody>
                        </Table>

                        <Pagination>
                            <Pagination.Prev onClick={() => paginate(currentPage - 1)} disabled={currentPage === 1} />
                            {Array.from({ length: totalPages }, (_, i) => (
                                <Pagination.Item key={i + 1} active={i + 1 === currentPage} onClick={() => paginate(i + 1)}>
                                    {i + 1}
                                </Pagination.Item>
                            ))}
                            <Pagination.Next onClick={() => paginate(currentPage + 1)} disabled={currentPage === totalPages} />
                        </Pagination>
                    </div>
                </div>
            </div>
            <ToastContainer />
        </PageLayout>
    );
};
